@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@300;600;700&display=swap");
.text-blue {
  color: #4f7df3;
}

.bg-blue {
  background-color: #4f7df3;
}

.text-pale-blue {
  color: #c2d3ff;
}

.bg-pale-blue {
  background-color: #c2d3ff;
}

.text-light-red {
  color: #ff5263;
}

.bg-light-red {
  background-color: #ff5263;
}

.text-gray {
  color: #969696;
}

.bg-gray {
  background-color: #969696;
}

.text-white {
  color: #fff;
}

.bg-white {
  background-color: #fff;
}

.text-dark-blue {
  color: #151f29;
}

.bg-dark-blue {
  background-color: #151f29;
}

.fw-light {
  font-weight: 300;
}

.fw-semi-bold {
  font-weight: 600;
}

.fw-bold {
  font-weight: 700;
}

html {
  font-style: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  bottom: 0;
}

ul {
  list-style-type: none;
}

button,
input {
  font-family: inherit;
}

a {
  text-decoration: none;
}

body {
  max-width: 100vw;
  min-height: 100vh;
  height: 100%;
  font-family: "Libre Franklin", sans-serif;
  position: relative;
}

.btn {
  border: none;
  width: 100%;
  max-width: 10.5rem;
  padding: 0.625rem;
  border-radius: 3.125rem;
  cursor: pointer;
  box-shadow: 0px 4px 5px rgba(79, 125, 243, 0.39);
  transition: all 0.3s ease-in;
}
.btn:hover {
  background-color: #7599f6;
}

.btn-snackbar {
  width: 12.5rem;
  height: 3.125rem;
  background-color: cornflowerblue;
  border: none;
  border-radius: 0.625rem;
  margin-top: 18.75rem;
  cursor: pointer;
  color: white;
  font-size: 1.25rem;
}

.snackbar {
  position: fixed;
  left: 50%;
  right: 50%;
  top: 5%;
  transform: translate(-50%, -50%);
  width: 21.875rem;
  height: 3.75rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  text-align: center;
}
.snackbar .icon {
  flex: 20%;
}
.snackbar .message {
  flex: 80%;
  text-align: start;
  font-weight: 700;
}

#show {
  visibility: visible;
  animation: fadeIn 0.5s, fadeOut 0.5s 2.5s;
}

#hide {
  visibility: hidden;
}

@keyframes fadeIn {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 1.875rem;
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    bottom: 1.875rem;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
form {
  position: relative;
}
form .form-group {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 0.625rem;
  width: 36.25rem;
}
form .form-group input {
  width: 100%;
  max-width: 25rem;
  height: 2.9375rem;
  border-radius: 3.125rem;
  border: 1px solid #969696;
  padding: 0 1.5625rem;
  outline: none;
}
form .form-group input.error-message {
  border: 1px solid #ff5263;
}
form .error {
  position: absolute;
  color: #ff5263;
  bottom: -1.125rem;
  left: 1.5625rem;
  font-size: 0.6875rem;
  font-style: italic;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
}
.header .logo {
  width: 100%;
  max-width: 5.375rem;
  margin-bottom: 2.5rem;
}
.header .title {
  font-size: 2.8125rem;
  margin-bottom: 0.625rem;
}
.header .description {
  font-size: 1.25rem;
  letter-spacing: -0.02em;
  margin-bottom: 2.1875rem;
}

.hero {
  width: 100%;
  max-width: 35rem;
  margin-bottom: 3.125rem;
}

.bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.bottom .icon-group {
  display: flex;
  gap: 0.9375rem;
  margin-bottom: 1.5625rem;
}
.bottom .icon-group .fa {
  position: relative;
  width: 1.5625rem;
  height: 1.5625rem;
  font-size: 0.8125rem;
  border: 1px solid #c2d3ff;
  border-radius: 50%;
  color: #4f7df3;
}
.bottom .icon-group .fa:hover {
  border: 1px solid #4f7df3;
  background: #4f7df3;
  color: #fff;
}
.bottom .icon-group .fa svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.bottom .icon-group .fa .visually-hidden {
  border: 0;
  padding: 0;
  margin: 0;
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 - a 0 height clip, off to the bottom right of the visible 1px box */
  clip: rect(1px, 1px, 1px, 1px);
  /*maybe deprecated but we need to support legacy browsers */
  clip-path: inset(50%);
  /*modern browsers, clip-path works inwards from each corner*/
  white-space: nowrap;
  /* added line to stop words getting smushed together (as they go onto seperate lines and some screen readers do not understand line feeds as a space */
}
.bottom .copyright {
  font-size: 0.8125rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
}
.attribution a {
  color: #3e52a3;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}